import React from 'react'
import { FC } from 'react'
import './Contact.css'
import iconTelegram from './icons/icon-telegram.png'
import iconInstagram from './icons/icon-instagram.png'
import iconGithub from './icons/icon-git.png'
import iconYoutube from './icons/icon-youtube.png'

const Contact : FC = () => {
    return (
        <div  className='contact'>
            <h1 data-aos='fade-down'>Связаться со мной</h1>
            <div className="contact-items">
                <div className="icons" data-aos='zoom-in'><img src={iconTelegram} alt="Telegram" /></div>
                <div className="icons" data-aos='zoom-in'><img src={iconInstagram} alt="Instagram" /></div>
                <div className="icons" data-aos='zoom-in'><img src={iconGithub} alt="YouTube" /></div>
                <div className="icons" data-aos='zoom-in'><img src={iconYoutube} alt="GitHub" /></div>
            </div>
        </div>
    )
}

export default Contact
